@mixin desktop(){
	@media (min-width: 1367px){
		@content
	}
}

@mixin ipadPro2{
	@media (max-width: 1366px){
		@content
	}
}

@mixin ipadPro{
	@media (max-width: 1024px){
		@content
	}
}

@mixin ipad2{
	@media (max-width: 922px){
		@content
	}
}

@mixin ipad{
	@media (max-width: 768px){
		@content
	}
}

@mixin iphone8{
	@media (max-width: 414px){
		@content
	}
}

@mixin iphone5{
	@media (max-width: 320px){
		@content
	}
}

/*nav*/
.navbar li:nth-child(4) a{
	color: #563529;
	@include iphone8(){
		color: #eee;
	}
}

/*banner start*/
.bg{
	width: 100%;
	background-image: url('../image/bg/about.png');
	background-size: 100%;
	background-repeat: no-repeat;
	background-position: 0 -100px;
	margin-top: 95px;
	@include desktop(){
		margin-top: 110px;
	}
	@media (max-width: 823px){
		margin-top: 65px;
	}
	@include iphone8(){
		margin-top: 80px;
	}
}
.bg figure{
	width: 50%;
	display: block;
	margin: auto;
	@include desktop(){
		width: 60%;
	}
	@include iphone8(){
		width: 100%;
	}
}
.bg img{
	width: 100%;
	vertical-align: middle;
	margin: auto;
}

/*banner end*/

/*====================================*/

/*news start*/
.news{
	width: 100%;
	padding: 10px;
	font-family: Microsoft JhengHei;
	background-color: rgb(69,43,33);
	div{
		margin-left: 75px;
	}
	h3{
		padding: 0 10px;
		color: #DECDA4;
		font-size: 16px;

		font-weight: normal;
		display: inline-block;
		border-right: 1px solid #ddd;
	}
	marquee{
		vertical-align: middle;
    width: 300px;
		margin-left: 2px;
		padding: 0 5px;
		font-size: 16px;
		color: #fff;
	}
	@include desktop(){
		h3{
			font-size: 24px;
		}
		marquee{
			font-size: 24px;
		}
	}
	@include ipadPro(){
		div{
			margin-left: 12px;
		}
	}
	@media (max-width: 823px){
		h3{
			font-size: 14px;
		}
		marquee{
			font-size: 14px;
		}
	}
	@include ipad(){
		div{
			margin-left: 15px;
		}
		h3{
			font-size: 14px;
		}
		marquee{
			font-size: 14px;
		}
	}
	@include iphone8(){
		div{
			margin: 0;
		}
		marquee{
			width: 200px;
		}
	}
	@include iphone5(){
		h3{
			font-size: 12px;
		}
		marquee{
			width: 180px;
			font-size: 12px;
		}
	}
}
/*news end*/

/*=======================================*/

/*main start*/
.mainArea{
	width: 100%;
	padding: 20px 50px;
	font-family: Microsoft JhengHei;
	background: url(../image/bg/bg_main.png);
	@include ipadPro(){
		padding: 20px 0;
	}
	@media (max-width: 823px){
		padding: 5px 0px;
	}
	@include ipad(){
		padding: 20px 20px;
	}
	@include iphone8(){
		padding: 20px 0;
	}
	
}
.mainBox{
	width: 90%;
	margin: auto;
	padding: 0 40px 20px;
	@include desktop(){
		padding: 20px 40px 20px;
	}
	@include ipadPro(){
		padding: 0 0 20px;
	}
	@include ipad(){
		width: 100%;
		padding: 0;
	}
	@include iphone8(){
		padding: 0 15px;
	}
}
.mainBox .best{
	width: 182px;
	font-size: 28px;
	position: relative;
	display: block;
	background: url('../image/know.png') no-repeat left center;
	background-size: 35px;
	background-position: 5px 10px;
	color: #452B21;
	margin: 20px 0 0;
	padding: 5px 0 0px 50px;
	line-height: 26px;

	@include desktop(){
		width: 270px;
		font-size: 40px;
		padding: 5px 0 0px 90px;
		line-height: 41px;
		background-size: 60px;
	}
	@media (max-width: 823px){
		font-size: 22px;
	}
	@include ipad(){
		margin: 0 0 0 20px;
	}
	@media (max-width: 736px){
		font-size: 20px;
	}
	@include desktop(){
		font-size: 40px;
	}
	@media (max-width: 640px){
		margin: 0;
	}
	@media (max-width: 568px){
		font-size: 18px;
	}
	@include iphone8(){
		font-size: 20px;
		line-height: 25px;
	}
	@include iphone5(){
		background-size: 30px;
		font-size: 16px;
		line-height: 22px;
	}
}

/*connecBox start*/
.connecBox{
	width: 100%;
	display: flex;
	padding: 20px;
	justify-content: space-around;
	flex-direction: row;
	border-top: 2px solid #784c3a;
	@include desktop(){
		padding: 40px;
	}
	@include ipadPro(){
		padding: 20px 0;
	}
	@include iphone8(){
		padding: 20px 0;
		justify-content: flex-start;
		flex-direction: column;
	}
}
.connecBox .info{
	width: 40%;
	/*flex-basis: 40%;
	flex-grow: 1;*/
	padding: 20px;
	@media (max-width: 823px){
		padding: 0;
	}
	@include ipad(){
		padding: 0;
	}
	@media (max-width: 640px){
		width: 45%;
	}
	@media (max-width: 667px){
		width: 50%;
	}
	@include iphone8(){
		width: 100%;
		padding: 0;
	}
}
.connecBox .info .item{
	padding: 10px;
	p{
		font-size: 16px;
		padding-left: 5px;
		text-align: center;
		display: inline-block;
		vertical-align: middle;
	}
	@include desktop(){
		padding: 20px;
		p{
			font-size: 24px;
		}
	}
	@media (max-width: 823px){
		p{
			font-size: 14px;
		}
	}
	@include ipad(){
		p{
			font-size: 14px;
		}
	}
	@media (max-width: 568px){
		padding: 5px;
		p{
			font-size: 12px;
		}
	}
	@include iphone8(){
		padding: 10px;
		p{
			font-size: 14px;
		}
	}
	@include iphone5(){
		p{
			font-size: 12px;
		}
	}
}
.item .icon{
	display: inline-block;
	vertical-align: middle;
}
.icon1,.icon5{
	font-size: 12px;
	@include desktop(){
		font-size: 14px;
	}
	@media (max-width: 823px){
		font-size: 8px;
	}
}
.icon2,.icon3{
	font-size: 14px;
	@include desktop(){
		font-size: 16px;
	}
	@media (max-width: 823px){
		font-size: 10px;
	}
}
.icon4{
	font-size: 15px;
	@include desktop(){
		font-size: 17px;
	}
	@media (max-width: 823px){
		font-size: 11px;
	}
}
.fab{
	color: #777;
}
.connecBox .map{
	width: 50%;
	/*flex-basis: 50%;
	flex-grow: 1;*/
	border: 1px solid #ddd;
	@media (max-width: 667px){
		width: 45%;
	}
	@include iphone8(){
		margin-top: 30px;
		width: 100%;
	}
}
.map iframe{
	width: 100%;
	height: 100%;
}
/*connecBox end*/

/*===========================*/

/*addressBox start*/
.addressBox{
	width: 95%;
	margin: auto;
	padding: 20px;
	border-top: 2px dashed #784c3a;
	display: flex;
	justify-content: space-around;
	flex-direction: row;

	@include iphone8(){
		justify-content: flex-start;
		flex-direction: column-reverse;
		padding: 30px 0;
	}
}
.add_pic{
	flex-basis: 45%;
	flex-grow: 1;
	margin: 0 5px;

	@include iphone8(){
		margin: 0;
	}
}
.add_pic figure{
	width: 85%;
	padding: 10px 0;
	margin: auto;
	@include iphone8(){
		width: 100%;
		padding: 20px 0;
	}
}
.add_pic figure img{
	width: 100%;
	vertical-align: middle;
}
.add_txt{
	flex-basis: 45%;
	flex-grow: 1;
	margin: 0 5px;
	padding: 10px 0;
	border-left: 1px solid #784c3a;
	h2{
		width: 100px;
		font-size: 24px;
		font-family: Microsoft JhengHei;
		font-weight: normal;
		margin: auto;
		text-align: center;
		border-bottom: 2px solid #784c3a;
	}

	@include desktop(){
		h2{
			width: 130px;
			font-size: 32px;
		}
	}
	@media (max-width: 823px){
		h2{
			width: 86px;
			font-size: 20px;
		}
	}
	@media (max-width: 736px){
		h2{
			width: 76px;
			font-size: 18px;
		}
	}
	@media (max-width: 568px){
		h2{
			width: 66px;
			font-size: 16px;
		}
	}
	@include iphone8(){
		width: 100%;
		margin: 0;
		padding: 10px 0 20px;
	}
	@include iphone5(){
		h2{
			width: 58px;
			font-size: 14px;
		}
	}
}
.add_txt .item{
	position: relative;
	right: 13px;
	margin-top: 45px;
	transition: all .3s;
	p{
		width: 88%;
		display: inline-block;
		padding-left: 10px;
		font-family: Microsoft JhengHei;
		vertical-align: middle;
		font-size: 16px;
	}

	@include desktop(){
		margin-top: 80px;
		p{
			font-size: 24px;
		}
	}
	@include ipadPro(){
		margin-top: 35px;
	}
	@media (max-width: 823px){
		margin-top: 18px;
		right: 9px;
		p{
			font-size: 14px;
		}
	}
	@include ipad(){
		margin-top: 20px;
	}
	@media (max-width: 736px){
		margin-top: 13px;
	}
	@media (max-width: 568px){
		p{
			font-size: 12px;
		}
	}
	@include iphone8(){
		p{
			font-size: 14px;
		}
	}
	@include iphone5(){
		p{
			font-size: 12px;
		}
	}
}
.add_txt .item:hover .icon{
	transition: .3s;
	animation: grow 1s linear infinite;
}
.add_txt .item:hover p{
	transition: all .3s;
	color: #784c3a;
}
.add_txt .item .icon{
	display: inline-block;
	i{
		font-size: 25px;
		color: #784c3a;
		border-radius: 50%;
		background: url(../image/bg/bg_main.png);
	}
	@media (max-width: 823px){
		i{
			font-size: 18px;		
		}
	}
}
/*addressBox end*/

/*===========================*/

/*messageBox start*/
.messageBox{
	width: 83%;
	margin: auto;
	padding: 30px;
	border-top: 2px dashed #784c3a;
	h2{
		font-size: 24px;
		font-weight: normal;
		text-align: center;
	}
	@include desktop(){
		h2{
			font-size: 32px;
		}
	}
	@media (max-width: 823px){
		h2{
			font-size: 20px;
		}
	}
	@media (max-width: 568px){
		h2{
			font-size: 16px;
		}
	}
	@include iphone8(){
		width: 95%;
		padding: 30px 0;
	}
	@include iphone5(){
		h2{
			font-size: 14px;
		}
	}
}
.mess_txt{
	padding: 10px 0;
	margin-top: 20px;
	text-align: center;
	div{
		margin: auto;
		padding: 10px 80px 10px 10px;
	}
	input{
		width: 40%;
		margin-left: 20px;
		border-radius: 5px;
		border:1px solid #ccc;
		box-shadow: 0px 0px 2px rgba(0,0,0,.2) ;
	}
	button{
		width: 60px;
		height: 30px;
		font-size: 16px;
		line-height: 32px;
		margin-top: 15px;
		background-color: rgba(111,68,54,1);
		border-radius: 5px;
		cursor: pointer;
	  text-decoration: none;
		border: none;
		a{
			padding: 3px 12px;
			border-radius: 5px;
			color: #fff;
			font-family: Microsoft JhengHei;
		}
	}

	@include desktop(){
		label{
			font-size: 24px;
		}
		input{
			padding: 5px;
		}
		button{
			width: 80px;
			height: 40px;
			font-size: 20px;
			margin-top: 30px;
			a{
				padding: 3px 18px;
			}
		}
	}
	@media (max-width: 823px){
		label{
			font-size: 14px;
		}
		input{
			width: 50%;
			margin-left: 25px;
		}
		button{
			width: 50px;
			height: 25px;
			line-height: 20px;
			a{
				font-size: 14px;
				padding: 1px 10px;
			}
		}
	}
	@media (max-width: 568px){
		label{
			font-size: 12px;
		}
		input{
			font-size: 14px;
		}
		button{
			width: 40px;
			height: 25px;
			line-height: 20px;
			a{
				font-size: 12px;
				padding: 1px 5px;
			}
		}
	}
	@include iphone8(){
		padding: 10px 0;
		margin-top: 0px;
		div{
			padding: 3px 0;
		}
		input{
			width: 80%;
			margin-left: 10px;
		}
		label{
			font-size: 14px;
		}
		button{
			width: 50px;
			height: 30px;
			line-height: 20px;
			a{
				font-size: 14px;
				padding: 5px 10px;
			}
		}
	}
	@include iphone5(){
		label{
			font-size: 12px;
		}
		button{
			width: 45px;
			height: 30px;
			line-height: 20px;
			a{
				font-size: 12px;
				padding: 5px 8px;
			}
		}
	}
}
.name{
	label{
		padding-left: 10px;
	}
}
.tel{
	label{
		padding-left: 10px;
	}
}
.message{
	label{
		padding-left: 10px;
	}
	textarea{
		width: 40%;
		vertical-align: top;
		margin-left: 20px;
		border-radius: 5px;
		border:1px solid #ccc;
		box-shadow: 0px 0px 2px rgba(0,0,0,.2) ;
		resize : none;
	}

	@media (max-width: 823px){
		textarea{
			width: 50%;
			margin-left: 25px;
		}
	}
	@include iphone8(){
		label{
			font-size: 14px;
			padding-left: 5px;
		}
		textarea{
			width: 80%;
			margin-left: 10px;
		}
	}
	@include iphone5(){
		label{
			font-size: 12px;
		}
	}
}

/*messageBox end*/
/*main end*/
/*===========================*/




#gotop {
    position: fixed;
    right: 20px;
    bottom: 30px;
    font-size: 40px;
    color: rgb(87,38,0);
    cursor: pointer;
    z-index: 1000;
    transition: 1s;
   	display: none;
   	animation: rubberBand 1s linear;

   	@include desktop(){
		right: 40px;
	  bottom: 60px;
	  font-size: 60px;
	}
}

#gotop:hover{
	transition: 1s;
	transform: translateY(-10px);
}




@keyframes grow{
	0%{
		transform: scale(.5);
	}
	100%{
		transform: scale(1);
	}
}

